<template>

  <div v-if="currentItem" class="edit-form">
    <h4>Edit </h4>
    <form>

      <div class="form-group">
        <label for="title"><strong>Item Id:  </strong></label>
        # {{ currentItem.id }}
      </div>

      <div class="form-group">
        <label for="title">Serial</label>
        <input type="text" class="form-control" id="serial" v-model="currentItem.serial"/>
      </div>

      <div class="form-group">
        <label for="title">Title</label>
        <input type="text" class="form-control" id="title" v-model="currentItem.title"/>
      </div>

      <div class="form-group">
        <label for="description">Description</label>

      <!-- <textarea rows = "6" cols = "60" type="text" class="block"   id="description" v-model="currentItem.description"/> -->
        <textarea type="text" class="form-control" id="title" v-model="currentItem.description"/>

        <!-- <input type="text" class="block"  id="description" v-model="currentItem.description"/> -->
      </div>

    </form>

    <button class="badge badge-danger mr-2" @click="deleteTutorial">Delete</button>

    <button type="submit" class="badge badge-success" @click="updateTutorial"> Update </button>

    <p>{{ message }}</p>

  </div>

  <div v-else>
    <br />
    <p class="p">Please click on a Tutorial...</p>
  </div>
</template>

<script>
import AboutClosetServices from "../services/AboutClosetServices"

export default {
  name: "tutorial",
  data() {
    return {
      currentItem: null,
      loading: true,
      errored: false,
      message: ''
    };
  },
  components :{},
  methods: {

    getAboutItem(id) {
      this.message = 'Loading...'
      AboutClosetServices.get(id)
        .then(response => {
          this.message = ''
          this.currentItem = response.data.about_closet;
          console.log(response.data);
        })
        .catch(e => {
          this.message = e
          console.log(e);
        });
    },


    updateTutorial() {
      this.message = 'Loading...'
      AboutClosetServices.update(this.currentItem.id, this.currentItem)
        .then(response => {
          this.message = ''
          console.log(response.data);
          this.$router.push({name : 'aboutustems'});
          this.message = 'Updated successfully!';
        })
        .catch(e => {
          this.message = e
          console.log(e);
        });
    },

    deleteTutorial() {
      AboutClosetServices.delete(this.currentItem.id)
        .then(response => {
          console.log(response.data);
          this.$router.push({ name: "tutorials" });
        })
        .catch(e => {
          console.log(e);
        });
    }
  },
  mounted() {
    this.message = '';
    this.getAboutItem(this.$route.params.id);
  }
};
</script>

